<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input v-model="keywords" @keyup.enter.native="search" placeholder="请输入用户名称/班级名称"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-col>
      <el-col :span="5" :offset="12">
        <el-button type="primary" @click="new_user">新建用户</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb10">
      <el-table
        :data="tableData"
        class="w24"
        @filter-change="binFilters"
        border
        height="500px"
        :default-sort="{prop: 'user_name', order: 'descending'}"
        :row-class-name="tableRowClassName"
        ref="filterTable"
      >
        <el-table-column prop="user_name" label="用户名称" sortable></el-table-column>
        <el-table-column prop="user_id" label="用户ID"></el-table-column>
        <el-table-column prop="user_phone" label="手机号码"></el-table-column>

        <el-table-column
          prop="user_sex_text"
          label="性别"
          :filters="[{ text: '男', value: '1' },
        { text: '女', value: '0' }]"
          column-key="sex"
          :filter-multiple="false"
        ></el-table-column>

        <el-table-column prop="role_name" label="角色" :filters="filtersRoles" column-key="filter_group_role_id"></el-table-column>

        <el-table-column
          prop="faculty_name"
          label="院系"
          :filters="filtersFaculties"
          column-key="filter_group_faculty_id"
        ></el-table-column>

        <el-table-column prop="clazz_name" label="班级"></el-table-column>

        <el-table-column
          prop="state_id_text"
          label="状态"
          :filters="[{ text: '可用', value: '1' },
        { text: '停用', value: '0' }]"
          column-key="state_id"
          :filter-multiple="false"
        ></el-table-column>

        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="change_info_user(scope.row)">编辑</el-button>
            <el-button size="mini" type="primary" @click="orle_user(scope.$index, scope.row)">角色分配</el-button>
            <el-button
              size="mini"
              :type="scope.row.state_id?'danger':'primary'"
              @click="handleDelete( scope.row,scope.$index,)"
            >{{scope.row.state_id == 1?'停用':'启用'}}用户</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row class="mt10 df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
// import pagination from "@/components/pagination.vue";
export default {
  // components: { pagination },
  data() {
    return {
      tableData: [], //表数据
      filtersRoles: [], //可以筛选的所有角色数组
      filtersFaculties: [], //可以筛选的所有院系数组
      filters: {
        filter_group_faculty_id: [],
        filter_group_role_id: [],
        state_id: "",
        sex: ""
      }, //已经选中的筛选数组
      keywords: "", //搜索输入
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 50 //当前一页选择的条数
    };
  },
  mounted() {
    this.getUsersList();
    this.$post("user/getgroupid").then(res => {
      res.roles.forEach(item => {
        item.text = item.role_name;
        item.value = item.role_id;
      });
      res.faculties.forEach(item => {
        item.text = item.faculty_name;
        item.value = item.faculty_id;
      });
      this.filtersRoles = res.roles;
      this.filtersFaculties = res.faculties;
    });
  },
  methods: {
    //触发筛选
    binFilters(res) {
      if ("filter_group_role_id" in res) {
        this.filters.filter_group_role_id = res.filter_group_role_id;
      } else if ("filter_group_faculty_id" in res) {
        this.filters.filter_group_faculty_id = res.filter_group_faculty_id;
      } else if ("state_id" in res) {
        this.filters.state_id = res.state_id[0];
      } else if ("sex" in res) {
        this.filters.sex = res.sex[0];
      }
      this.getUsersList();
    },
    //新建用户
    new_user() {
      this.$router.push({
        path: "/page/user/userNew",
        query: {}
      });
    },
    //修改用户信息
    change_info_user(res) {
      this.$router.push({
        path: "/page/user/userInfoChange",
        query: {}
      });
    },
    //点击角色分配的时候触发
    orle_user() {
      this.$router.push("/page/user/userRole");
    },
    //点击搜索时候触发
    search() {
      this.$get("users/keywords", {
        keywords: this.keywords,
        current_page: this.current_page,
        page_size: this.page_size
      }).then(res => {
        this.$refs.filterTable.clearFilter();
        this.filters.filtersRole = [];
        this.filters.filtersStatus = [];
        this.filters.filtersOffice = [];
        this.tableData = res.data;
        this.total = res.total;
      });
    },
    //点击功能禁用和启用触发
    handleDelete(row, index) {
      this.$confirm(
        "确定" + (row.status == "1" ? "停用" : "启用") + "用户?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).then(() => {
        this.$put(
          row.status == "1" ? "users/freezeUser" : "users/unFreezeUser",
          { id: row.id }
        ).then(res => {
          this.getUsersList();
        });
      });
    },
    //遍历过滤数据
    tableRowClassName({ row, rowIndex }) {
      Object.assign(row, row.faculty, row.clazz, row.role);
      if (row.user_sex) {
        row.user_sex_text = "男";
      } else {
        row.user_sex_text = "女";
      }
      if (row.state_id == "0") {
        row.state_id_text = "可用";
        return "";
      } else {
        row.state_id_text = "停用";
        return "warning-row";
      }
    },
    //该表一页的大小
    handleSizeChange(res) {
      this.page_size = res;
      this.getUsersList();
    },
    //换页
    handleCurrentChange(res) {
      this.current_page = res;
      this.getUsersList();
    },
    //筛选列表请求或者普通列表请求
    getUsersList() {
      this.$post("user/getuser", {
        ...this.filters,
        keywords: this.keywords,
        page: this.current_page,
        paging: this.page_size
      }).then(res => {
        this.tableData = res.lists;
        this.total = res.total;
      });
    }
  }
};
</script>

<style>
.el-table .warning-row {
  background: oldlace !important;
}
</style>


